body {
  margin: 0;
  margin-bottom: 100px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(./assets/images/fruitbackgroundAVIF.avif);
}

.wrapper {
  margin-bottom: 100px;
  text-align: -webkit-center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.success {
  text-align: center;
  background-color: #fffcf3;
  width: fit-content;
  margin: 30px;
  border-radius: 5px;
  padding: 10px;
}

#dancingFruit {
  background-image: linear-gradient(to left,
      #93ff68,
      #e4ff55,
      #ffb543,
      #ff5a5a,
      #c660ff);
  padding: 5px;
}

/* #footer {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
} */
.footer-container {
  justify-content: center !important;
}

.dashboardCard {
  margin: 20px;
}

.myName {
  background-image: linear-gradient(to left,
      #93ff68,
      #e4ff55,
      #ffb543,
      #ff5a5a,
      #c660ff);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.sortButtons {
  display: flex !important;
  justify-content: center !important;
}

.productImage {
  width: 100% !important;
  height: 200px !important;
  object-fit: cover !important;
  border-radius: 10px !important;
}

.login-error {
  color: rosybrown;
  font-size: medium;
  font-style: italic;
  text-align: center;
}

.cardBackground {
  background-color: #fffcf3;
}

.removeContainer {
  display: flex !important;
  justify-content: space-between !important;
}

.checkoutContainer {
  background-color: #fffcf3;
  padding: 10px;
  border-radius: 5px;
  display: flex !important;
  justify-content: space-between !important;
}

.centerContainer {
  display: flex !important;
  justify-content: center !important;
}

.center {
  width: fit-content;
  background-color: #fffcf3;
  border-radius: 7px;
  padding: 10px;
  margin: 20px;
  color: #ff5a5a;
  font-weight: bolder;
  font-size: 50px;
}

